.feedback {
  width: 100%;
  margin-top: 55px;

  &__outter {
    max-width:  1280px;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 0 30px;
  }

  &__inner {
    position: relative;
    padding: 50px 20px;
    border: 8px solid $gray-light;

    &:before {
      content: "";
      position: absolute;
      left: 5%;
      top: -8px;
      width: 31%;
      border-bottom: 8px solid $gray-light-ultimate;
    }

    &:after {
      content: "";
      position: absolute;
      left: 36%;
      bottom: -8px;
      width: 57%;
      border-top: 8px solid $gray-light-ultimate;
    }

  }

  &__title {
    margin: 0;
    font-size: $font-size-bg-x;
  }

  &__text {
    font-size: $font-size-md;
    font-weight: $font-weight-md;
    opacity: 0.7;
  }

}
